


import { useEffect, useState, useRef } from 'react';

import { lbbConfig } from './config';
import EImage from './assets/img/image.svg';
import EAudio from './assets/img/audio.svg';
import EVideo from './assets/img/video.svg';
import Default from './Const/Components/layout/Default';
import Masonry from './Const/Components/layout/Masonry';

const dummyImage = "https://bblockswp.com/wp-content/demo/img/green-hill.jpg";

const Lightbox = ({ ContentArea, attributes, isBackend = false, custom, clientId, Modal }) => {

	const { lightboxType, items, layout, columns, button, galleryIcon, popupOptions, cId, popupIconLeft, popupIconMiddle, popupIconRight, thumb } = attributes;
	const { mixAllData } = popupOptions;
	const [isOpen, setOpen] = useState(false);
	const [activeIndex, setActiveIndex] = useState(0);
	const containerRef = useRef();

	// funcyapps feature
	// useEffect(() => {
	// 	lbbConfig(clientId, popupIconLeft, popupIconMiddle, popupIconRight, thumb);
	// }, [popupIconLeft, popupIconMiddle, popupIconRight, thumb]);

	useEffect(() => {
		lbbConfig(clientId);
	}, [clientId]);

	const filteredItems = 'button' === lightboxType ? [items[0]] : items;

	const ButtonContent = () => <>
		{button.icon?.class && <i className={`icon ${button.icon?.class} `}></i>}
		{button.text}
	</>
	// check lightboxType
	if (!lightboxType) {
		return false
	}

	useEffect(() => {
		if (layout === 'default') {
			document.querySelector(`#lbbLightBox-${cId} .lbbLightBox`).style.height = 'auto';
		}
	}, [layout]);

	return <div ref={containerRef} className={` ${lightboxType === 'gallery' ? `lbbLightBox ${layout} columns-${columns.desktop} columns-tablet-${columns.tablet} columns-mobile-${columns.mobile}` : 'lbbLightBox llbButton'}`}>
		{layout === 'masonry' ? <Masonry containerRef={containerRef} attributes={attributes} filteredItems={filteredItems} mixAllData={mixAllData} clientId={clientId} EImage={EImage} dummyImage={dummyImage} ButtonContent={ButtonContent} EAudio={EAudio} EVideo={EVideo} galleryIcon={galleryIcon} setOpen={setOpen} setActiveIndex={setActiveIndex} isOpen={isOpen} custom={custom} isBackend={isBackend} activeIndex={activeIndex} ContentArea={ContentArea} Modal={Modal} /> :
			<Default attributes={attributes} filteredItems={filteredItems} mixAllData={mixAllData} clientId={clientId} EImage={EImage} dummyImage={dummyImage} ButtonContent={ButtonContent} EAudio={EAudio} EVideo={EVideo} galleryIcon={galleryIcon} setOpen={setOpen} setActiveIndex={setActiveIndex} isOpen={isOpen} custom={custom} isBackend={isBackend} activeIndex={activeIndex} ContentArea={ContentArea} Modal={Modal} />}

	</div >
}
export default Lightbox;
